<template>
    <yd-layout>
        <yd-navbar title="待阅已阅文件" bgcolor="#70b6ef" color="#fff" fontsize="0.36rem">
            <router-link to="#" slot="left">
                <yd-navbar-back-icon color="#fff">
                    <slot color="#fff">返回</slot>
                </yd-navbar-back-icon>
            </router-link>
        </yd-navbar>
        <div class="listContent">
            <div class="listWidth">
                <yd-list theme="5">
                    <yd-list-item type="div" v-for="item in waitedList" :key="item.title">
                        <span slot="title" class="span_title" :class="readType" v-text="item.title"></span>
                        <div slot="other">
                            <a v-bind:href="item.url">
                                <yd-cell-group>
                                    <yd-cell-item arrow>
                                    </yd-cell-item>
                                </yd-cell-group>
                            </a>
                        </div>
                        <yd-list-other slot="other">
                            <div>
                                <span class="span_publisher">发布者：{{item.sysname}}</span>
                                <span class="span_date" v-text="item.date"></span>
                            </div>
                        </yd-list-other>
                    </yd-list-item>
                    <!-- 待阅加载的更多信息 -->
                    <yd-list-item type="div" v-for="item in moreWait" :key="item.title" v-if="BtnTxt == '已阅'">
                        <span slot="title" class="span_title" v-text="item.title"></span>
                        <div slot="other">
                            <a v-bind:href="item.url">
                                <yd-cell-group>
                                    <yd-cell-item arrow>
                                    </yd-cell-item>
                                </yd-cell-group>
                            </a>
                        </div>
                        <yd-list-other slot="other">
                            <div>
                                <span class="span_publisher">发布者：{{item.sysname}}</span>
                                <span class="span_date" v-text="item.date"></span>
                            </div>
                        </yd-list-other>
                    </yd-list-item>
                    <!-- 已阅加载的更多信息 -->
                    <yd-list-item type="div" v-for="item in moreHas" :key="item.title" v-if="BtnTxt == '待阅'">
                        <span slot="title" class="span_title" :class="readType" v-text="item.title"></span>
                        <div slot="other">
                            <a v-bind:href="item.url">
                                <yd-cell-group>
                                    <yd-cell-item arrow>
                                    </yd-cell-item>
                                </yd-cell-group>
                            </a>
                        </div>
                        <yd-list-other slot="other">
                            <div>
                                <span class="span_publisher">发布者：{{item.sysname}}</span>
                                <span class="span_date" v-text="item.date"></span>
                            </div>
                        </yd-list-other>
                    </yd-list-item>
                </yd-list>
            </div>
        </div>
        <div class="more_news">
            <div class="click_more" v-if="moreFlag == true" v-on:click="more_List()">
                <a href="javascript:;" class="more_a">查看更多历史公告</a>
            </div>
            <div class="read_btn" v-bind:class="{'read_btn02':BtnTxt=='待阅'}">
                <a href="javascript:;" v-on:click="BtnTxt=='已阅'?BtnTxt='待阅':BtnTxt='已阅';toggleBtn()" class="read_btn_a" v-text="BtnTxt"></a>
            </div>
        </div>
    </yd-layout>
</template>
<script>
export default {
    name: 'waitRead',
    data() {
        return {
            BtnTxt: '已阅',
            waitedList: [],
            moreWait: [],
            moreHas: [],
            hasPageNo: 1,
            waitPageNo: 1,
            moreFlag: true,
            readType: ''
        }
    },
    mounted() {
        document.setTitle('待阅已阅文件');
        this.waitedRead();
    },
    methods: {
        waitedRead() {
            var vm = this;
            vm.get(vm.conf.appliCenter + '/API/WorkListWS/WaitDispose/list?', {
                'name': 'b2bdc5a759803444',
                'sysstate': '3',
                'pageSize': '7',
                'pageNo': 1
            }, function (res) {
                var data = res.jsonData;
                vm.waitedList = data;
                // console.log(res);
            });
        },
        toggleBtn() {
            var vm = this;
            if (vm.BtnTxt == '已阅') {
                vm.readType = '';
                vm.moreWait = [];
                vm.waitedRead();
            } else if (vm.BtnTxt == '待阅') {
                vm.hasWait = [];
                vm.moreFlag = true;
                vm.readType = 'readtype';
                vm.get(vm.conf.appliCenter + '/API/WorkListWS/WaitDispose/list?', {
                    'name': 'b2bdc5a759803444',
                    'sysstate': '4',
                    'pageSize': '7',
                    'pageNo': 1
                }, function (res) {
                    var data = res.jsonData;
                    vm.waitedList = data;
                });
            }
        },
        more_List() {
            var vm = this;
            if (vm.BtnTxt == '已阅') {
                vm.readType = '';
                vm.waitPageNo = vm.waitPageNo + 1;
                vm.get(vm.conf.appliCenter + '/API/WorkListWS/WaitDispose/list?', {
                    'name': 'b2bdc5a759803444',
                    'sysstate': '3',
                    'pageSize': '7',
                    'pageNo': vm.waitPageNo
                }, function (res) {
                    var data = res.jsonData;
                    // console.log(data.length);
                    if (data.length == 0) {
                        vm.moreFlag = false;
                    }
                    for (var i = 0; i < data.length; i++) {
                        var item = data[i];
                        vm.moreWait.push(item);
                    }
                    // console.log(vm.moreFlag);
                });
            } else if (vm.BtnTxt == '待阅') {
                vm.readType = 'readtype';
                vm.hasPageNo = vm.hasPageNo + 1;
                vm.get(vm.conf.appliCenter + '/API/WorkListWS/WaitDispose/list?', {
                    'name': 'b2bdc5a759803444',
                    'sysstate': '4',
                    'pageSize': '7',
                    'pageNo': vm.hasPageNo
                }, function (res) {
                    var data = res.jsonData;
                    if (data.length == 0) {
                        vm.moreFlag = false;
                    }
                    for (var i = 0; i < data.length; i++) {
                        var item = data[i];
                        vm.moreHas.push(item);
                    }
                    // console.log(vm.hasPageNo);
                });
            }
        }
    }
}
</script>
<style scoped>
.news_date {
    padding-left: 0.3rem;
    background: #ffc674;
    height: 0.59rem;
    color: #fff;
    font-size: 0.28rem;
    line-height: 0.59rem;
}

.span_title {
    font-size: 0.3rem;
    display: block;
    width: 6.09rem;
    height: 0.45rem;
    overflow: hidden;
}

.span_publisher {
    font-size: 0.26rem;
}

.span_date {
    font-size: 0.26rem;
    padding-left: 1.14rem;
    padding-right: 0.32rem;
}

.span_time {
    font-size: 0.26rem;
}

.read_btn {
    width: 1.04rem;
    height: 0.44rem;
    background: url(../../../static/images/trs_yl_0921.png) #ffa740;
    background-repeat: no-repeat;
    background-size: 0.26rem;
    background-position: 0.06rem 0.06rem;
    border-radius: .5rem;
    position: fixed;
    bottom: 0.3rem;
    right: 0.24rem;
    z-index: 5;
}

.read_btn02 {
    background: url(../../../static/images/trs_yl_0921.png) #4aa9f6;
    background-repeat: no-repeat;
    background-size: 0.26rem;
    background-position: 0.06rem 0.06rem;
}

.read_btn .read_btn_a {
    color: #fff;
    font-size: .22rem;
    line-height: .44rem;
    padding-left: 0.4rem;
    width: 1.04rem;
    height: 0.44rem;
    display: block;
}

.readtype {
    color: #999;
}
</style>


